.pacifico-regular {
  font-family: Pacifico, cursive;
  font-style: normal;
  font-weight: 400;
}

body {
  color: var(--general-color);
  background-color: var(--white-color);
  font-family: Open Sans;
  font-size: 14px;
  font-weight: 400;
  line-height: 150%;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.general-title {
  color: var(--second-color);
  font-size: 24px;
  font-weight: 800;
}

.title {
  color: var(--second-color);
  font-size: 20px;
  font-weight: 600;
  line-height: 100%;
}

.button-frame {
  color: var(--general-color);
  border: 1px solid var(--general-color);
  border-radius: 4px;
  padding: 1px 10px;
  font-family: Inter;
  font-size: 10px;
  font-weight: 600;
  line-height: 166%;
}

.button-inherit {
  color: var(--white-color);
  border: 2px solid #000;
  font-family: Inter;
  font-size: 20px;
  font-weight: 600;
  line-height: 160%;
}

.button-blue {
  color: var(--white-color);
  border: 3px solid var(--button-color);
  background: var(--button-color);
  border-radius: 8px;
  font-family: Inter;
  font-size: 20px;
  font-weight: 600;
  line-height: 160%;
}

.button-grey {
  color: var(--general-color);
  background: rgba(95, 105, 128, .2);
  border: 1px solid rgba(95, 105, 128, .2);
  border-radius: 8px;
  font-family: Inter;
  font-size: 20px;
  font-weight: 600;
  line-height: 160%;
}

.hidden {
  overflow: hidden;
}

@media screen and (min-width: 479.98px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 767.98px) {
  .container {
    padding-left: 40px;
    padding-right: 40px;
  }
}

@media screen and (min-width: 991.98px) {
  body {
    font-size: 20px;
  }

  .container {
    width: 992px;
    padding-left: 40px;
    padding-right: 40px;
  }

  .general-title {
    font-size: 80px;
  }

  .title {
    font-size: 48px;
  }

  .button-frame {
    font-size: 12px;
  }

  .button-inherit {
    border: 3px solid var(--white-color);
    border-radius: 8px;
    font-size: 42px;
  }

  .button-blue {
    border: 3px solid var(--button-color);
    border-radius: 8px;
    font-size: 26px;
  }

  .button-grey {
    border: 3px solid rgba(95, 105, 128, .2);
    border-radius: 8px;
    font-size: 26px;
  }
}

@media screen and (min-width: 1439.98px) {
  body {
    font-size: 24px;
  }

  .container {
    width: 1440px;
    padding-left: 80px;
    padding-right: 80px;
  }

  .general-title {
    font-size: 100px;
  }

  .title {
    font-size: 60px;
  }
}

* {
  border: 0;
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

:focus, :active, a :focus, a :active {
  outline: none;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  height: 100%;
  width: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  font-size: 14px;
  line-height: 1;
}

input, button, textarea {
  font-family: inherit;
}

input ::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button ::-moz-focus-inner {
  border: 0;
  padding: 0;
}

a, a :visited, a :hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: 400;
}

:root {
  --white-color: #fdfdfd;
  --general-color: #5f6980;
  --second-color: #001648;
  --button-color: #019fab;
}

.header__line {
  border-bottom: 1px solid rgba(95, 105, 128, .3);
}

.header__body {
  justify-content: space-between;
  padding-top: 18px;
  padding-bottom: 17px;
  display: flex;
}

.header__wrapper-social {
  display: none;
}

.header__social-list {
  column-gap: 20px;
  display: flex;
}

.headder__item-social {
  width: 32px;
  height: 32px;
  cursor: pointer;
  border: 1px solid rgba(95, 105, 128, .8);
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  display: none;
}

.icon-fb {
  width: 20px;
  height: 24px;
  fill: rgba(95, 105, 128, .8);
}

.icon-inst {
  width: 20px;
  height: 20px;
  fill: rgba(95, 105, 128, .8);
}

.icon-telegram {
  width: 24px;
  height: 22px;
  fill: rgba(95, 105, 128, .8);
}

.header__wrapper-input {
  border-bottom: 1px solid rgba(95, 105, 128, .3);
  display: none;
  position: relative;
}

.header__search {
  width: 268px;
  color: var(--button-color);
  margin-bottom: 10px;
}

.header__search::-ms-input-placeholder {
  color: var(--general-color);
  font-size: 16px;
  font-style: italic;
  font-weight: 300;
}

.header__search::placeholder {
  color: var(--general-color);
  font-size: 16px;
  font-style: italic;
  font-weight: 300;
}

.icon-input {
  width: 24px;
  height: 24px;
  fill: rgba(95, 105, 128, .6);
  cursor: pointer;
}

.wrapper-logo {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.icon-logo {
  width: 32px;
  height: 32px;
  fill: rgba(95, 105, 128, .2);
}

.header__name-cake {
  color: rgba(95, 105, 128, .8);
  margin-bottom: 14px;
  font-size: 40px;
  font-weight: 600;
  line-height: 60%;
}

.header__name-logo {
  color: rgba(95, 105, 128, .8);
  font-size: 16px;
  font-style: italic;
}

.wrapper__user {
  justify-content: flex-end;
  display: flex;
}

.header__wrapper-tel {
  align-items: center;
  display: none;
}

.wrapper__tel {
  width: 32px;
  height: 32px;
  border: 1px solid rgba(95, 105, 128, .8);
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  margin-right: 16px;
  display: flex;
}

.icon-tel {
  width: 20px;
  height: 20px;
  fill: var(--general-color);
}

.header__tel {
  color: var(--general-color);
  font-weight: 300;
}

.wrapper__block {
  align-items: flex-end;
  column-gap: 20px;
  display: flex;
}

.header__wrapper-search {
  cursor: pointer;
  display: none;
}

.wrapper__search {
  width: 32px;
  height: 32px;
  border: 1px solid rgba(95, 105, 128, .8);
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.icon-serch {
  width: 24px;
  height: 24px;
  fill: var(--general-color);
}

.header__wrapper-login {
  color: var(--general-color);
  cursor: pointer;
  align-items: center;
  font-size: 16px;
  font-style: italic;
  font-weight: 300;
  display: none;
}

.wrapper__login {
  width: 32px;
  height: 32px;
  border: 1px solid rgba(95, 105, 128, .8);
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.icon-login {
  width: 16px;
  height: 16px;
  fill: var(--general-color);
}

.login__text {
  margin-left: 6px;
  display: none;
}

.header__wrapper-cart {
  color: var(--general-color);
  cursor: pointer;
  align-items: center;
  font-size: 16px;
  font-style: italic;
  font-weight: 300;
  display: flex;
}

.wrapper__cart {
  width: 32px;
  height: 32px;
  border: 1px solid rgba(95, 105, 128, .8);
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.blue-circle {
  content: "";
  width: 24px;
  height: 24px;
  background-color: var(--button-color);
  color: var(--white-color);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-style: normal;
  display: flex;
  position: absolute;
  top: -50%;
  right: -50%;
}

.icon-cart {
  width: 20px;
  height: 20px;
  fill: var(--general-color);
}

.cart__text {
  margin-left: 6px;
  display: none;
}

.header__wrapper-burger {
  cursor: pointer;
}

.icon-burger {
  width: 32px;
  height: 32px;
  font: var(--general-color);
}

@media screen and (min-width: 479.98px) {
  .header__wrapper-social {
    align-items: flex-end;
    display: flex;
  }

  .headder__item-social {
    display: flex;
  }
}

@media screen and (min-width: 767.98px) {
  .wrapper__user {
    flex-direction: column;
  }

  .header__wrapper-tel {
    margin-bottom: 25px;
    display: flex;
  }

  .header__wrapper-search {
    display: block;
  }

  .header__wrapper-login {
    display: flex;
  }
}

@media screen and (min-width: 991.98px) {
  .header__body {
    padding-bottom: 23px;
  }

  .header__wrapper-social {
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }

  .header__wrapper-input {
    margin-top: 24px;
    display: flex;
  }

  .icon-fb, .icon-inst, .icon-telegram {
    transition: fill .45s cubic-bezier(.4, 0, .2, 1);
  }

  .headder__item-social {
    transition: border .45s cubic-bezier(.4, 0, .2, 1);
  }

  .headder__item-social:hover {
    border: 1px solid rgba(1, 159, 171, .8);
  }

  .headder__item-social:hover .icon-fb, .headder__item-social:hover .icon-inst, .headder__item-social:hover .icon-telegram {
    fill: rgba(1, 159, 171, .8);
  }

  .icon-input {
    transition: fill .45s cubic-bezier(.4, 0, .2, 1);
  }

  .header__wrapper-input {
    transition: border-bottom .45s cubic-bezier(.4, 0, .2, 1);
  }

  .header__wrapper-input:hover {
    border-bottom: 1px solid rgba(1, 159, 171, .8);
  }

  .header__wrapper-input:hover .icon-input {
    fill: rgba(1, 159, 171, .8);
  }

  .header__wrapper-search {
    display: none;
  }

  .wrapper__tel {
    transition: border .45s cubic-bezier(.4, 0, .2, 1);
  }

  .icon-tel {
    transition: fill .45s cubic-bezier(.4, 0, .2, 1);
  }

  .header__tel {
    transition: color .45s cubic-bezier(.4, 0, .2, 1);
  }

  .header__wrapper-tel:hover .wrapper__tel {
    border: 1px solid rgba(1, 159, 171, .8);
  }

  .header__wrapper-tel:hover .icon-tel {
    fill: rgba(1, 159, 171, .8);
  }

  .header__wrapper-tel:hover > .header__tel {
    color: rgba(1, 159, 171, .8);
  }

  .wrapper__search {
    transition: border .45s cubic-bezier(.4, 0, .2, 1);
  }

  .icon-search {
    transition: fill .45s cubic-bezier(.4, 0, .2, 1);
  }

  .header__wrapper-search:hover .wrapper__search {
    border: 1px solid rgba(1, 159, 171, .8);
  }

  .header__wrapper-search:hover .icon-search {
    fill: rgba(1, 159, 171, .8);
  }

  .wrapper__login {
    transition: border .45s cubic-bezier(.4, 0, .2, 1);
  }

  .icon-login {
    transition: fill .45s cubic-bezier(.4, 0, .2, 1);
  }

  .header__wrapper-login:hover .wrapper__login {
    border: 1px solid rgba(1, 159, 171, .8);
  }

  .header__wrapper-login:hover .icon-login {
    fill: rgba(1, 159, 171, .8);
  }

  .header__wrapper-login:hover > .login__text {
    color: rgba(1, 159, 171, .8);
  }

  .login__text {
    transition: color .45s cubic-bezier(.4, 0, .2, 1);
    display: block;
  }

  .wrapper__cart {
    transition: border .45s cubic-bezier(.4, 0, .2, 1);
  }

  .icon-cart {
    transition: fill .45s cubic-bezier(.4, 0, .2, 1);
  }

  .header__wrapper-cart:hover .wrapper__cart {
    border: 1px solid rgba(1, 159, 171, .8);
  }

  .header__wrapper-cart:hover .icon-cart {
    fill: rgba(1, 159, 171, .8);
  }

  .header__wrapper-cart:hover > .cart__text {
    color: rgba(1, 159, 171, .8);
  }

  .cart__text {
    transition: color .45s cubic-bezier(.4, 0, .2, 1);
    display: block;
  }

  .header__wrapper-burger {
    display: none;
  }

  .header__name-cake {
    font-size: 60px;
  }

  .header__name-logo {
    font-size: 24px;
  }

  .wrapper__block {
    column-gap: 56px;
  }

  .header__tel {
    font-size: 24px;
  }
}

.main {
  flex-direction: column;
  display: flex;
}

.swiper {
  max-width: 100%;
  order: 2;
}

.image-slide {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.main__container {
  order: 1;
  padding-bottom: 40px;
}

.main__wraper-content {
  width: 100%;
  z-index: 10;
  flex-direction: column;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.main__title-cake {
  color: var(--second-color);
  margin-bottom: 8px;
  font-family: Pacifico, cursive;
  font-size: 32px;
  font-weight: 800;
  line-height: 150%;
}

.main__text {
  color: var(--second-color);
  font-size: 16px;
  font-weight: 600;
  line-height: 150%;
  display: none;
}

.main__button {
  background-color: rgba(0, 0, 0, .314);
  border-radius: 25px;
  padding: 8px 32px;
}

@media screen and (min-width: 479.98px) {
  .main__wraper-content {
    align-items: flex-start;
  }

  .main__title-cake {
    margin-bottom: 4px;
    font-size: 32px;
  }

  .main__text {
    margin-bottom: 12px;
    display: block;
  }

  .main__button {
    width: 266px;
  }
}

@media screen and (min-width: 767.98px) {
  .main__wraper-content {
    padding-left: 40px;
  }

  .main__title-cake {
    font-size: 48px;
  }

  .main__text {
    font-size: 20px;
  }

  .main__button {
    width: 320px;
    border: 3px solid #000;
    border-radius: 40px;
    padding: 20px 26px;
    font-size: 26px;
  }
}

@media screen and (min-width: 991.98px) {
  .main__wraper-content {
    width: 992px;
    margin: 0 auto;
  }

  .main__title-cake {
    font-size: 80px;
  }

  .main__text {
    width: 798px;
    margin-bottom: 24px;
    font-size: 24px;
  }

  .main__button {
    transition: background-color .35s cubic-bezier(.4, 0, .2, 1);
  }

  .main__button:hover {
    background-color: var(--button-color);
    color: var(--white-color);
  }
}

@media screen and (min-width: 1439.98px) {
  .main__wraper-content {
    width: 1440px;
    padding-left: 80px;
  }

  .main__button {
    width: 360px;
  }

  .main__text {
    margin-bottom: 56px;
  }
}

@media screen and (min-width: 320px) and (max-width: 479px) {
  .image-slide {
    height: 300px;
    object-fit: cover;
  }
}

.another-color {
  color: #001648;
  font-weight: 700;
}

.position-text-main {
  padding-top: 40px;
  padding-bottom: 24px;
}

@media screen and (min-width: 479.98px) {
  .position-text-main {
    padding-top: 60px;
    padding-bottom: 40px;
  }
}

.link-main {
  color: #282828;
  font-family: Open Sans;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
}

@media screen and (min-width: 479.98px) {
  .link-main {
    display: none;
  }
}

.secondary-title {
  color: #001648;
  margin-top: 24px;
  margin-bottom: 20px;
  font-family: Open Sans;
  font-size: 20px;
  font-weight: 600;
  line-height: 100%;
}

@media screen and (min-width: 479.98px) {
  .secondary-title {
    margin-top: 0;
    margin-bottom: 40px;
    font-size: 32px;
  }
}

@media screen and (min-width: 767.98px) {
  .secondary-title {
    font-size: 48px;
  }
}

@media screen and (min-width: 991.98px) {
  .secondary-title {
    font-size: 60px;
  }
}

.first-paragraph-hero, .second-paragraph-hero, .third-paragraph-hero {
  color: #5f6980;
  margin-bottom: 20px;
  font-family: Open Sans;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
}

.fourth-paragraph-hero {
  color: #5f6980;
  font-family: Open Sans;
  font-size: 14px;
  font-weight: 400;
  line-height: 150%;
}

@media screen and (min-width: 479.98px) {
  .first-paragraph-hero, .second-paragraph-hero, .third-paragraph-hero, .fourth-paragraph-hero {
    font-size: 16px;
    line-height: 150%;
  }
