@media screen and (min-width: 767.98px) {
  .first-paragraph-hero, .second-paragraph-hero, .third-paragraph-hero, .fourth-paragraph-hero {
    font-size: 20px;
  }
}

@media screen and (min-width: 991.98px) {
  .first-paragraph-hero, .second-paragraph-hero, .third-paragraph-hero, .fourth-paragraph-hero {
    font-size: 24px;
  }
}

.team__container {
  padding-bottom: 24px;
}

.team__img-wrapper {
  margin-bottom: 40px;
}

.team__item-card, .team__card-img {
  width: 100%;
}

.team__card-thumb {
  z-index: 20;
  background: rgba(255, 255, 255, .75);
  margin-top: 8px;
  padding: 8px;
  position: relative;
}

.team__wrapper-name {
  justify-content: space-between;
  margin-bottom: 4px;
  display: flex;
}

.team__name {
  color: var(--second-color);
  letter-spacing: -.275px;
  font-family: Inter;
  font-size: 20px;
  font-weight: 600;
  line-height: 110%;
}

.team__wrapper-icon {
  width: 32px;
  height: 32px;
  border: 1px solid var(--general-color);
  border-radius: 40px;
  justify-content: center;
