  display: flex;
}

.team__tag-link {
  color: var(--general-color);
}

.team__title {
  margin-bottom: 20px;
}

.team__list {
  flex-direction: column;
  row-gap: 15px;
  display: flex;
}

.team__item {
  line-height: 150%;
}

@media screen and (min-width: 479.98px) {
  .team__title {
    font-size: 32px;
  }

  .team__item-card {
    max-width: 100%;
  }

  .team__name {
    font-size: 24px;
  }

  .team__item-tag {
    font-size: 12px;
  }
}

@media screen and (min-width: 767.98px) {
  .team__container {
    padding-bottom: 40px;
  }

  .team__body {
    column-gap: 20px;
    display: flex;
  }

  .team__img-wrapper {
    max-width: 50%;
    margin-bottom: 0;
  }

  .team__content {
    max-width: 50%;
    align-self: center;
  }

  .team__title {
    font-size: 48px;
  }

  .team__item {
    font-size: 20px;
  }
}

@media screen and (min-width: 991.98px) {
  .team__body {
    column-gap: 24px;
  }

  .team__item-card {
    width: 443px;
  }

  .team__wrapper-img {
    width: 443px;
    overflow: hidden;
  }

  .team__card-img {
    width: 443px;
    transition: transform .7s cubic-bezier(.4, 0, .2, 1);
  }

  .team__card-img:hover {
    transform: scale(1.1);
  }

  .team__card-thumb {
    width: 420px;
    margin-top: -152px;
    margin-left: 12px;
    margin-right: 12px;
    padding: 32px;
  }

  .team__name {
    font-size: 28px;
    transition: color .35s cubic-bezier(.4, 0, .2, 1);
  }

  .team__name:hover {
    color: var(--button-color);
  }

  .team__wrapper-icon {
    width: 40px;
    height: 40px;
    cursor: pointer;
  }

  .team__card-icon {
    width: 28px;
    height: 28px;
  }

  .team__list-tag {
    gap: 12px;
  }

  .team__item-tag {
    cursor: pointer;
    transition: color .35s cubic-bezier(.4, 0, .2, 1);
  }

  .team__item-tag:hover {
    color: var(--button-color);
    border: 1px solid var(--button-color);
  }

  .team__item-tag:hover .team__tag-link {
    color: var(--button-color);
  }

  .team__title {
    margin-bottom: 40px;
    font-size: 48px;
  }
}

@media screen and (min-width: 1439.98px) {
  .team__item-card {
    min-width: 628px;
  }

  .team__wrapper-img, .team__card-img {
    width: 628px;
