.contact__contact-container {
    padding-top: 40px;
    padding-bottom: 24px;
}
.contact__contact-list {
    display: flex;
    align-items: center;
    margin-bottom: 24px;
}
.contact__contact-item {
    display: flex;
    align-items: center;
    cursor: pointer;
}
.contact__contact-link {
    color: var(--general-color);
    font-size: 12px;
    line-height: 133.333%;
}
.is-active {
    color: var(--second-color);
    line-height: 133.333%;
}
.contact__contact-body {
    margin-bottom: 24px;
}
.contact__contact-title {
    margin-bottom: 20px;
    color: var(--second-color);
    font-family: "Open Sans";
    font-size: 20px;
    font-weight: 600;
    line-height: 100%;
}
.contact__address {
    margin-bottom: 20px;
}
.contact__subtitle {
    margin-bottom: 12px;
    color: var(--second-color);
    line-height: 150%;
    font-weight: 600;
}
.contact__link-address {
    color: var(--general-color);
    line-height: 150%;
}
.contact__tel {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}
.conatct__link-tel {
    color: var(--general-color);
    line-height: 150%;
}
.conatct__email {
    margin-bottom: 20px;
}
.conatct__link-email {
    color: var(--general-color);
    line-height: 150%;
}
.conatct__social-list {
    display: flex;
    column-gap: 24px;
}
.conatct__item-social {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;

    border-radius: 6px;
    border: 1px solid rgba(95, 105, 128, 0.80);
}
.contact__feedback-body {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.contact__feedback-title {
    margin-bottom: 12px;
}
.contact__feedback-form {
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.contact__feedback-wrapper {
    display: flex;
    flex-direction: column;
    row-gap: 20px;
}
.contact__feedback-name,
.contact__feedback-event,
.contact__feedback-comment {
    padding: 15px 0px;
    color: var(--button-color);
    font-family: "Open Sans";
    font-size: 16px;
    line-height: 137.5%;
    border-bottom: 1px solid var(--second-color);
    &::placeholder {
        color: var(--general-color);
        font-family: 'Inter';
        font-size: 16px;
        line-height: 162.5%;
    }
}
.contact__feedback-wrapper-input {
    display: flex;
    border: 1px solid var(--second-color);
    border-radius: 6px;
}
.contact__feedback-label-phone {
    padding: 15px 16px;
    color: var(--general-color);
    font-family: 'Inter';
    font-size: 16px;
    line-height: 162.5%;
}
.contact__feedback-phone {
    padding: 15px 10px;
    color: var(--button-color);
    font-family: "Open Sans";
    font-size: 16px;
    line-height: 137.5%;
    border-left: 1px solid var(--second-color);
    &::placeholder {
        color: var(--general-color);
        font-family: 'Inter';
        font-size: 16px;
        line-height: 162.5%;
    }
}
.contact__feedback-button {
    padding: 13px 13px;
    background-color: inherit;
}
@media screen and (min-width: 479.98px) {
    .contact__contact-body {
        display: flex;
        flex-direction: column;
    }
    .conatct__wrapper-contacts {
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
    }
    .contact__tel,
    .contact__address,
    .conatct__email,
    .contact__social {
        width: 190px;
        margin-bottom: 0px;
    }
    .contact__feedback-phone {
        min-width: 380px;
    }
}
@media screen and (min-width: 768.98px) {
    .contact__contact-container {
        padding-bottom: 40px;
    }
    .contact__contact__wrapper {
        display: flex;
    }
    .contact__contact-body,
    .contact__feedback {
        width: 50%;
    }
    .contact__contact-link {
        font-size: 16px;
    }
    .contact__contact-title {
        font-size: 48px;
    }
    .contact__subtitle {
        font-size: 20px;
    }
    .contact__link-address,
    .conatct__link-tel,
    .conatct__link-email {
        font-size: 16px;
    }
    .contact__feedback-title {
        margin-bottom: 25px;
    }
    .contact__feedback-text {
        margin-bottom: 25px;
    }
    .contact__feedback-form {
        gap: 20px;
    }
    .contact__feedback-phone {
        min-width: 285px;
    }
}
@media screen and (min-width: 991.98px) {
    .contact__contact-item {
        &:hover {
            text-decoration: underline;
        }
    }
    .contact__link-address,
    .conatct__link-tel,
    .conatct__link-email {
        transition: color 350ms cubic-bezier(0.4, 0, 0.2, 1);
        &:hover {
            color: var(--button-color);
        }
    }
    .icon-fb,
    .icon-inst,
    .icon-telegram {
        transition: fill 350ms cubic-bezier(0.4, 0, 0.2, 1);
    }
    .conatct__item-social {
        transition: border 350ms cubic-bezier(0.4, 0, 0.2, 1);
        &:hover {
            border: 1px solid rgba(1, 159, 171, 0.8);
        }
        &:hover .icon-fb{
            fill: var(--button-color);
        }
        &:hover .icon-inst{
            fill: var(--button-color);
        }
        &:hover .icon-telegram{
            fill: var(--button-color);
        }
    }
    .contact__feedback-wrapper {
        flex-direction: row;
        column-gap: 24px;
    }
    .contact__feedback-name,
    .contact__feedback-event {
        width: 50%;
        transition: border-bottom 350ms cubic-bezier(0.4, 0, 0.2, 1);
        &:hover {
            border-bottom: 1px solid var(--button-color);
        }
        &:focus {
            border-bottom: 1px solid var(--button-color);
        } 
    }
    .contact__feedback-comment {
        transition: border-bottom 350ms cubic-bezier(0.4, 0, 0.2, 1);
        &:hover {
            border-bottom: 1px solid var(--button-color);
        }
        &:focus {
            border-bottom: 1px solid var(--button-color);
        } 
    }
    .contact__feedback-phone {
        min-width: 385px;
        padding: 15px 16px;
        border-left: 1px solid var(--second-color);
        transition: border-left 350ms cubic-bezier(0.4, 0, 0.2, 1);
    }
    .contact__feedback-wrapper-input {
        border: 1px solid var(--second-color);
        border-radius: 6px;
        transition: border 350ms cubic-bezier(0.4, 0, 0.2, 1);
        &:hover {
            border: 1px solid var(--button-color);
        }
        &:hover .contact__feedback-phone {
            border-left: 1px solid var(--button-color);
        }
    }
    .contact__feedback-button {
        transition: background-color 350ms cubic-bezier(0.4, 0, 0.2, 1);
        &:hover {
            background-color: var(--button-color);
            color: var(--white-color);
        }
    }
}
@media screen and (min-width: 1439.98px) {
    .contact__feedback-phone {
        min-width: 570px;
    }
}